@import "settings"
@import "_mixins/_mixins"
@import "_components/ui"

/* main page */
.main-video
  margin-top: 50px
  @media (max-width: 639px)
    margin-top: 80px
  @media (max-width: 420px)
    margin-top: 40px
  &__title
    max-width: 400px
    line-height: 94%
    @media (max-width: 639px)
      max-width: 100%
    @media (max-width: 420px)
      max-width: 300px
  &__content
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    &__left-side
      flex: 0 0 47%
      max-width: 47%
      position: relative
      @media (max-width: 639px)
        flex: 0 0 100%
        max-width: 100%
      .js-video-link
        display: block
      .video-preview
        display: block
        width: auto
        max-width: 100%
        margin: 0
        border-radius: 32px
        @media (max-width: 639px)
          width: 100%
        @media (max-width: 420px)
          border-radius: 22px
      .circle-link
        position: absolute
        top: -72px
        left: -32px
        @media (max-width: 1023px)
          left: -25px
          top: -95px
          svg
            transform: scale(.8)
        @media (max-width: 420px)
          left: -50px
          top: -82px
          svg
            transform: scale(.55)
        .circle-text
          transform-origin: center
          animation: spin 10s linear infinite
    &__right-side
      flex: 0 0 53%
      max-width: 53%
      padding: 10px 0 0 50px
      @media (max-width: 1199px)
        padding: 0 0 0 25px
      @media (max-width: 639px)
        flex: 0 0 100%
        max-width: 100%
        padding: 0
        margin-top: 25px
      @media (max-width: 420px)
        margin-top: 30px
      .descr-block
        margin-top: 15px
        font-weight: 400
        font-size: 24px
        line-height: 125%
        letter-spacing: -.02em
        @media (max-width: 1199px)
          font-size: 22px
          margin-top: 20px
        @media (max-width: 959px)
          font-size: 18px
        @media (max-width: 420px)
          font-size: 16px
          margin-top: 25px
      .video-btn
        display: inline-block
        margin-top: 35px
        @media (max-width: 1199px)
          margin-top: 20px
        @media (max-width: 420px)
          margin-top: 15px
.main-success
  margin-top: 120px
  @media (max-width: 1199px)
    margin-top: 80px
  @media (max-width: 767px)
    margin-top: 40px
  .section-title-row
    .btns-row
      display: none
      @media (max-width: 575px)
        display: block
        margin: 0 0 0 auto
      .nav-btns
        display: flex
        align-items: center
        justify-content: flex-start
        margin-left: 30px
        .nav-btn
          display: flex
          align-items: center
          justify-content: center
          flex: 0 0 46px
          width: 46px
          height: 46px
          background-color: $purple-color
          transition: background-color .3s, color .3s
          color: #fff
          border-radius: 50%
          &.next-btn
            transform: scale(-1,1)
            margin-left: 17px
            @media (max-width: 419px)
              margin-left: 10px
          &:hover
            background-color: $yellow-btn-color
            color: #000
  &__content
    &.swiper
      overflow: visible
      padding: 35px 0
      @media (max-width: 575px)
        overflow: hidden
      @media (max-width: 419px)
        padding: 15px 0
    .swiper-wrapper
      display: flex
      align-items: stretch
      justify-content: flex-start
      flex-wrap: wrap
      @media (max-width: 575px)
        flex-wrap: nowrap
    .slider-pagination
      display: none
      align-items: center
      justify-content: center
      position: absolute
      bottom: 0 !important
      left: 0
      margin: 0 auto
      width: 100%
      max-width: 100%
      z-index: 1
      @media (max-width: 575px)
        display: flex
      .swiper-pagination-bullet
        width: 10px
        height: 10px
        margin: 0 7px !important
        background-color: #D9D9D9
        opacity: 1
        transition: background-color .3s
        &.swiper-pagination-bullet-active
          background-color: $purple-color
    &__item.swiper-slide
      flex: 0 0 calc(33.3% - 10px)
      max-width: calc(33.3% - 10px)
      margin: -20px 15px -20px 0
      transition: background-color .3s, box-shadow .3s
      padding: 30px
      display: flex
      flex-direction: column
      align-items: center
      justify-content: center
      border-radius: 32px
      cursor: default
      min-height: 425px
      @media (max-width: 1023px)
        margin: 0 15px 15px 0
        padding: 30px 20px
        min-height: 325px
        height: auto
      @media (max-width: 959px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
        margin: 0 20px 20px 0
        padding: 20px
        min-height: auto
      @media (max-width: 575px)
        flex: 0 0 auto
        width: auto
        max-width: 100%
        margin: 0
      &:nth-of-type(3n)
        margin-right: 0
        @media (max-width: 959px)
          margin-right: 20px
        @media (max-width: 575px)
          margin-right: 0
      &:nth-of-type(2n)
        @media (max-width: 959px)
          margin-right: 0
      .image-block
        .item-icon
          display: block
          width: auto
          max-width: 100%
          margin: 0 auto
          transition: filter .3s
      .descr-block
        margin-top: 18px
        .item-title
          font-family: DINCondensed, sans-serif
          font-weight: 400
          font-size: 24px
          line-height: 104%
          letter-spacing: .03em
          text-transform: uppercase
          text-align: center
          transition: color .3s
        .item-descr
          font-weight: 400
          font-size: 16px
          line-height: 22px
          letter-spacing: -.02em
          text-align: center
          margin-top: 24px
          transition: color .3s
      &:hover
        @media (min-width: 1023px)
          background-color: $main-red-color
          box-shadow: 0px 27px 80px rgba(0, 0, 0, 0.1), 0px 11.28px 33.4221px rgba(0, 0, 0, 0.0718854), 0px 6.0308px 17.869px rgba(0, 0, 0, 0.0596107), 0px 3.38082px 10.0172px rgba(0, 0, 0, 0.05), 0px 1.79553px 5.32008px rgba(0, 0, 0, 0.0403893), 0px 0.747159px 2.21381px rgba(0, 0, 0, 0.0281146)
          .image-block
            .item-icon
              filter: brightness(0) invert(1)
          .descr-block
            .item-title
              color: #fff
            .item-descr
              color: #fff

.main-support
  margin-top: 75px
  @media (max-width: 1199px)
    margin-top: 45px
  @media (max-width: 1023px)
    margin-top: 15px
  @media (max-width: 575px)
    margin-top: 60px
  @media (max-width: 419px)
    margin-top: 45px
  &__content
    margin-top: 30px
    @media (max-width: 419px)
      margin-top: 0
    .support-slider
      padding-left: calc(((100vw - 1110px) / 2) - 8px)
      @media (max-width: 1200px)
        padding-left: calc(((100vw - 1110px) / 2))
      @media (max-width: 1199px)
        padding-left: calc(((100vw - 960px) / 2) + 15px)
      @media (max-width: 959px)
        padding-left: 15px
      .swiper-wrapper
        display: flex
        align-items: stretch
        padding-top: 30px
        padding-bottom: 150px
        @media (max-width: 1023px)
          padding-top: 15px
          padding-bottom: 110px
        @media (max-width: 959px)
          padding-bottom: 60px
        @media (max-width: 419px)
          padding-bottom: 40px
        .slide-item
          display: flex
          align-items: stretch
          justify-content: flex-start
          max-width: 512px
          min-height: 323px
          height: auto
          border: 1px solid #F7F4F4
          box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.03), 0px 36.5016px 29.2013px rgba(0, 0, 0, 0.0206994), 0px 17.7209px 14.1767px rgba(0, 0, 0, 0.0166887), 0px 8.6871px 6.94968px rgba(0, 0, 0, 0.0133113), 0px 3.43489px 2.74791px rgba(0, 0, 0, 0.00930055)
          border-radius: 8px
          position: relative
          @media (max-width: 1023px)
            box-shadow: 0px 70px 80px rgba(0, 0, 0, 0.03), 0px 36.5016px 29.2013px rgba(0, 0, 0, 0.0206994), 0px 17.7209px 14.1767px rgba(0, 0, 0, 0.0166887), 0px 8.6871px 6.94968px rgba(0, 0, 0, 0.0133113), 0px 3.43489px 2.74791px rgba(0, 0, 0, 0.00930055)
          @media (max-width: 959px)
            box-shadow: 0px 0 40px rgba(0, 0, 0, 0.03), 0px 36.5016px 29.2013px rgba(0, 0, 0, 0.0206994), 0px 17.7209px 14.1767px rgba(0, 0, 0, 0.0166887), 0px 8.6871px 6.94968px rgba(0, 0, 0, 0.0133113), 0px 3.43489px 2.74791px rgba(0, 0, 0, 0.00930055)
            &:last-child
              margin-right: 15px
          @media (max-width: 767px)
            min-height: 280px
          @media (max-width: 575px)
            flex-direction: column
            max-width: calc(100% - 15px)
            height: auto
            min-height: auto

          &.empty
            opacity: 0
            visibility: hidden
          .number-row
            display: flex
            display: -webkit-flex
            align-items: center
            justify-content: center
            -webkit-justify-content: center
            -webkit-align-items: center
            text-size-adjust: none
            width: 59px
            height: 59px
            text-align: center
            border-radius: 50%
            border: 4px solid $red-title-color
            color: $red-title-color
            font-family: DINCondensed, sans-serif
            font-weight: 700
            font-size: 32px
            line-height: normal
            vertical-align: middle
            -webkit-appearance: none
            letter-spacing: -.02em
            position: absolute
            left: -26px
            top: 52px
            transition: background-color .3s, color .3s
            @media (max-width: 1200px)
              left: 20px
              top: 20px
            @media (max-width: 575px)
              width: 46px
              height: 46px
              flex: 0 0 46px
              border: 3px solid $red-title-color
              font-size: 30px
            @media (max-width: 419px)
              top: 35px
              left: 15px
              font-size: 24px
          &:after
            content: ''
            display: block
            width: 100%
            height: 5px
            background-color: $gray-color
            position: absolute
            bottom: -1px
            left: 0
            border-radius: 0px 0px 5.5px 5.5px
            transition: background-color .3s
          .image-block
            display: flex
            align-items: flex-end
            justify-content: flex-start
            flex: 0 0 50%
            max-width: 50%
            padding-left: 22px
            @media (max-width: 575px)
              flex: 0 0 auto
              max-width: 100%
              padding-left: 0
              order: 1
              margin-top: auto
            .slide-image
              display: block
              width: auto
              max-width: 100%
              @media (max-width: 767px)
                margin: 0 auto
          .descr-block
            flex: 0 0 50%
            max-width: 50%
            padding: 62px 30px 15px 0
            flex-direction: column
            align-items: flex-start
            justify-content: flex-start
            @media (max-width: 767px)
              padding: 35px 15px
            @media (max-width: 575px)
              flex: 0 0 auto
              max-width: 100%
              padding: 20px 15px 15px 90px
            @media (max-width: 419px)
              padding: 35px 15px 15px 80px
            .slide-title
              font-family: DINCondensed, sans-serif
              font-weight: 400
              font-size: 24px
              line-height: 25px
              letter-spacing: .03em
              text-transform: uppercase
            .slide-descr
              margin-top: 42px
              font-weight: 400
              font-size: 14px
              line-height: 20px
              @media (max-width: 575px)
                margin-top: 20px
              @media (max-width: 419px)
                margin-top: 15px
          &:hover
            &:before
              background-color: $red-title-color
              color: #fff
            &:after
              background-color: $red-title-color
      .slider-pagination
        display: none
        align-items: center
        justify-content: center
        position: absolute
        bottom: 0
        left: 0
        margin: 0 auto
        width: 100%
        max-width: calc(100% - 15px)
        z-index: 1
        @media (max-width: 959px)
          display: flex
        .swiper-pagination-bullet
          width: 10px
          height: 10px
          margin: 0 7px !important
          background-color: #D9D9D9
          opacity: 1
          transition: background-color .3s
          &.swiper-pagination-bullet-active
            background-color: $purple-color
  .scrollbar-wrapper
    position: relative
    @media (max-width: 959px)
      display: none
    .slider-scrollbar.swiper-scrollbar
      bottom: 50px
      background: none
      border-bottom: 4px dotted $gray-color
      height: 10px
    .swiper-scrollbar-drag
      background: none
      text-align: center
    .scrollbar-btn
      display: inline-flex
      align-items: center
      justify-content: flex-start
      background-color: $purple-color
      color: #fff
      font-family: DINCondensed, sans-serif
      font-weight: 400
      font-size: 24px
      line-height: 25px
      letter-spacing: .03em
      text-transform: uppercase
      padding: 9px 18px
      border-radius: 24px
      margin: -15px auto
      position: relative
      cursor: pointer
      white-space: nowrap
      @media (max-width: 1199px)
        font-size: 20px
      @media (max-width: 1023px)
        font-size: 18px
        padding: 9px 15px
      .icon
        margin-left: 40px
        font-size: 15px
        line-height: 25px
        @media (max-width: 1024px)
          margin-left: 15px

.main-partners
  margin-top: 65px
  @media (max-width: 1199px)
    margin-top: 45px
  @media (max-width: 959px)
    margin-top: 65px
  @media (max-width: 419px)
    margin-top: 45px
    position: relative
    &:after
      content: ''
      display: block
      width: 100%
      height: 3px
      max-width: calc(100% - 30px)
      position: absolute
      bottom: 0
      left: 15px
      background: linear-gradient(270deg,#da0c39 2.11%,rgba(218,12,57,0) 98.45%)
  &__content
    padding: 60px 0
    display: flex
    flex-wrap: wrap
    align-items: flex-start
    justify-content: flex-start
    @media (max-width: 575px)
      padding: 30px 0
    @media (max-width: 419px)
      padding: 15px 0
    &__item
      flex: 0 0 25%
      max-width: 25%
      display: flex
      align-items: flex-end
      justify-content: center
      min-height: 80px
      margin: 0 0 40px
      @media (max-width: 959px)
        flex: 0 0 33.3%
        max-width: 33.3%
      @media (max-width: 575px)
        flex: 0 0 50%
        max-width: 50%
        margin: 0 0 25px
      @media (max-width: 419px)
        margin: 0 0 15px
        min-height: 70px
      .partner-image
        display: block
        width: auto
        max-width: 100%
        margin: 0 auto
        @media (max-width: 767px)
          padding: 0 15px

.main-hitmenu
  margin-top: 35px
  position: relative
  @media (max-width: 1199px)
    margin-top: 0
  @media (max-width: 575px)
    padding-bottom: 85px
  @media (max-width: 419px)
    margin-top: 55px
    padding-bottom: 100px
  &__title-row
    display: flex
    align-items: center
    justify-content: flex-start
    .btns-row
      display: flex
      align-items: center
      justify-content: flex-start
      flex: 0 0 auto
      width: auto
      margin: 0 0 0 auto
      .menu-link
        margin-right: 24px
        @media (max-width: 575px)
          position: absolute
          bottom: 0
          left: 15px
          width: 100%
          max-width: calc(100% - 30px)
          text-align: center
      .nav-btns
        display: flex
        align-items: center
        justify-content: flex-start
        margin-left: 30px
        .nav-btn
          display: flex
          align-items: center
          justify-content: center
          flex: 0 0 57px
          width: 57px
          height: 57px
          background-color: $purple-color
          transition: background-color .3s, color .3s
          color: #fff
          border-radius: 50%
          @media (max-width: 575px)
            flex: 0 0 46px
            width: 46px
            height: 46px
          &.next-btn
            transform: scale(-1,1)
            margin-left: 17px
            @media (max-width: 419px)
              margin-left: 10px
          &:hover
            background-color: $yellow-btn-color
            color: #000
  &__content
    margin-top: 70px
    @media (max-width: 1023px)
      margin-top: 40px
    .hit-slider
      position: relative
      @media (max-width: 419px)
        padding: 0 15px
      .slide-item
        position: relative
        &:after
          content: ''
          display: block
          width: 1px
          height: 100%
          background-color: rgba(#000, .1)
          position: absolute
          top: 0
          right: -55px
          @media (max-width: 959px)
            right: -18px
          @media (max-width: 419px)
            display: none
        .image-block
          display: flex
          align-items: center
          justify-content: center
          min-height: 215px
          .slide-image
            display: block
            width: auto
            max-width: 100%
            margin: 0 auto
        .descr-block
          padding: 40px 0
          max-width: 250px
          @media (max-width: 419px)
            padding: 15px 0
          .item-name
            text-transform: uppercase
            font-family: DINCondensed, sans-serif
            font-weight: 400
            font-size: 24px
            line-height: 24px
            letter-spacing: .03em
          .item-descr
            margin-top: 15px
            font-size: 14px
            line-height: 20px
      .slider-pagination
        display: flex
        align-items: center
        justify-content: center
        margin-top: 32px
        @media (max-width: 419px)
          margin-top: 50px
        .swiper-pagination-bullet
          width: 10px
          height: 10px
          margin: 0 7px !important
          background-color: #D9D9D9
          opacity: 1
          transition: background-color .3s
          &.swiper-pagination-bullet-active
            background-color: $purple-color

.main-provider
  margin-top: 115px
  overflow: hidden
  @media (max-width: 1199px)
    margin-top: 75px
  @media (max-width: 1023px)
    margin-top: 45px
  @media (max-width: 575px)
    margin-top: 65px
  @media (max-width: 419px)
    margin-top: 45px
    padding-top: 65px
    position: relative
    &:before
      content: ''
      display: block
      width: 100%
      height: 3px
      max-width: calc(100% - 30px)
      position: absolute
      top: 0
      left: 15px
      background: linear-gradient(270deg,#da0c39 2.11%,rgba(218,12,57,0) 98.45%)
  &__content
    display: flex
    flex-wrap: wrap
    align-items: flex-start
    justify-content: flex-start
    @media (max-width: 959px)
      align-items: center
    &__title
      flex: 0 0 calc(28% - 10px)
      max-width: calc(28% - 10px)
      padding-top: 10px
      @media (max-width: 959px)
        padding-top: 0
        flex: 0 0 100%
        max-width: 100%
        margin-bottom: 20px
    &__descr
      flex: 0 0 calc(35% + 25px)
      max-width: calc(35% + 25px)
      font-size: 23px
      line-height: 32px
      letter-spacing: -.02em
      padding-top: 15px
      @media (max-width: 1199px)
        padding-left: 20px
      @media (max-width: 1023px)
        padding-left: 0
      @media (max-width: 959px)
        flex: 0 0 40%
        max-width: 40%
        font-size: 20px
        line-height: 28px
        padding: 30px 0
      @media (max-width: 767px)
        flex: 0 0 55%
        max-width: 55%
        padding: 15px 0
      @media (max-width: 575px)
        flex: 0 0 100%
        max-width: 100%
      a
        color: $main-red-color
        display: inline-block
        text-decoration: underline
        &:hover
          text-decoration: none
      p
        margin-top: 35px
        @media (max-width: 767px)
          margin-top: 20px
        &:nth-of-type(1)
          margin-top: 0
    &__image
      flex: 0 0 calc(37% - 15px)
      max-width: calc(37% - 15px)
      padding-left: 55px
      @media (max-width: 959px)
        flex: 0 0 60%
        max-width: 60%
        padding-left: 35px
      @media (max-width: 767px)
        flex: 0 0 45%
        max-width: 45%
        padding-left: 15px
      @media (max-width: 575px)
        flex: 0 0 100%
        max-width: 100%
        padding-left: 0
        margin-top: 20px
      .provider-image
        display: block
        width: auto
        @media (max-width: 959px)
          width: 120%
        @media (max-width: 767px)
          width: 135%
        @media (max-width: 419px)
          width: 125%
.main-cities
  font-family: DINCondensed, sans-serif
  font-weight: 400
  font-size: 70px
  line-height: 68px
  text-transform: uppercase
  margin-top: 100px
  overflow: hidden
  @media (max-width: 1199px)
    margin-top: 55px
  @media (max-width: 959px)
    font-size: 55px
    line-height: 60px
    margin-top: 35px
  @media (max-width: 575px)
    margin-top: 45px
  @media (max-width: 419px)
    font-size: 42px
    line-height: 45px
  &__line
    display: flex
    align-items: center
    white-space: nowrap
    animation: scroll_right 45s linear 1s infinite
    position: relative
    justify-content: flex-end
    &:nth-of-type(2)
      justify-content: flex-start
      animation: scroll_left 45s linear 1s infinite
    li
      display: inline-block
      color: rgba(#000, .8)

      &:nth-of-type(2n)
        color: rgba(#000, .3)
      &:nth-of-type(3n)
        color: rgba(#000, .5)
      &:before
        content: '/'
        color: rgba(#FDC62C, .8)

/* other pages */
.main-block
  @media (max-width: 959px)
    overflow: hidden
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    min-height: 422px
    @media (max-width: 1199px)
      min-height: 355px
    @media (max-width: 959px)
      min-height: 300px
    @media (max-width: 767px)
      min-height: 290px
    &__left-side
      flex: 0 0 38%
      max-width: 38%
      padding-top: 25px
      @media (max-width: 639px)
        flex: 0 0 100%
        max-width: 100%
      @media (max-width: 420px)
        padding-top: 5px
      .page-subtitle
        margin-top: 35px
        @media (max-width: 959px)
