      &:before
        content: ''
        display: block
        width: 100%
        height: 1px
        background-color: #F0F0F0
        position: absolute
        top: 0
        left: 0
        transition: background-color .3s
      &:after
        content: ''
        display: block
        width: 1px
        height: 100%
        background-color: #F0F0F0
        position: absolute
        bottom: 0
        right: 0
        transition: background-color .3s
