          border-radius: 10px
      &:last-child
        border-radius: 10px 10px 33px 10px
        @media (max-width: 767px)
          border-radius: 10px
      &:nth-of-type(2n)
        &:after
          display: none
      &:nth-of-type(3n)
        &:after
          display: none
        @media (max-width: 767px)
          &:after
            display: block
          &:before
            content: ""
            display: block
            width: 100%
            height: 1px
            background-color: #f0f0f0
            position: absolute
            top: 0
            left: 0
            transition: background-color .3s
