/* import animates here */
@import "fade_in_down"
@import "fade_in_left"
@import "fade_in_right"
@import "fade_in"
@import "spin"

@keyframes scroll_right
  0%
    transform: translateX(0)
  100%
    transform: translateX(100%)
@keyframes scroll_left
  0%
    transform: translateX(0)
  100%
    transform: translateX(-100%)