@import "../settings"

.section-title-row
  display: flex
  align-items: center
  justify-content: flex-start
  width: 100%
  position: relative
  &:after
    content: url("../images/common/svg/title_btn.svg")
    display: flex
    align-items: center
    justify-content: flex-end
    flex: 1 1 auto
    max-width: 100px
    height: 3px
    padding-top: 3px
    background: linear-gradient(270deg, #DA0C39 2.11%, rgba(218, 12, 57, 0) 98.45%)
    position: relative
    top: 3px
    margin: 0 3px 0 30px
    transition: max-width 3s
    @media (max-width: 639px)
      display: none
  &.animated
    &:after
      max-width: 100%
.section-title
  display: inline-block
  font-family: DINCondensed, sans-serif
  font-weight: 400
  font-size: 42px
  line-height: 110%
  letter-spacing: -.02em
  color: #000
  position: relative
  @media (max-width: 1023px)
    font-size: 38px
  @media (max-width: 767px)
    font-size: 34px
  @media (max-width: 420px)
    font-size: 32px
    line-height: 32px
.btn-style
  font-family: DINCondensed, sans-serif
  font-weight: 400
  font-size: 18px
  line-height: 25px
  letter-spacing: .03em
  text-transform: uppercase
  color: #000
  background-color: $yellow-color
  transition: background-color .3s
  padding: 18px 30px 14px
  border-radius: 30px
  border: none
  &:hover
    background-color: $yellow-color-hover
.page-title
  font-family: DINCondensed, sans-serif
  font-weight: 400
  font-size: 75px
  line-height: 84%
  color: #fff
  letter-spacing: -.02em
  @media (max-width: 1200px)
    font-size: 65px
  @media (max-width: 1024px)
    font-size: 60px
  @media (max-width: 959px)
    font-size: 55px
  @media (max-width: 640px)
    font-size: 50px
  @media (max-width: 575px)
    font-size: 42px
.page-subtitle
  font-family: DINCondensed, sans-serif
  font-weight: 400
  font-size: 37px
  line-height: 103%
  color: #fff
  letter-spacing: -.02em
  @media (max-width: 1200px)
    font-size: 32px
  @media (max-width: 959px)
    font-size: 28px
