.footer
  background-color: $main-red-color
  border-radius: 17px 17px 0 0
  width: 100%
  max-width: 1404px
  margin: 32px auto 0
  border-top: 22px solid $yellow-btn-color
  padding-bottom: 40px
  @media (max-width: 959px)
    padding-bottom: 20px
  @media (max-width: 420px)
    margin: 45px auto 0
  &__contacts
    margin-top: 45px
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    border-bottom: 2px solid rgba(#fff, .3)
    padding-bottom: 12px
    @media (max-width: 959px)
      margin-top: 25px
      padding-bottom: 0
    @media (max-width: 575px)
      position: relative
    @media (max-width: 420px)
      margin-top: 35px
    &__left-side
      flex: 0 0 35%
      max-width: 35%
      @media (max-width: 1024px)
        flex: 0 0 40%
        max-width: 40%
      @media (max-width: 959px)
        flex: 0 0 50%
        max-width: 50%
      @media (max-width: 575px)
        flex: 0 0 100%
        max-width: 100%
      .footer-title
        color: #fff
        font-family: DINCondensed, sans-serif
        font-weight: 400
        font-size: 42px
        line-height: 46px
        letter-spacing: -.02em
      .phone-link
        display: inline-block
        margin-top: 60px
        color: #fff
        font-family: DINCondensed, sans-serif
        font-size: 38px
        line-height: 38px
        letter-spacing: -.02em
        transition: color .3s
        @media (max-width: 959px)
          margin-top: 25px
        &:hover
          color: $yellow-btn-color
      p
        margin-top: 10px
        color: #fff
        font-size: 22px
        line-height: 20px
        letter-spacing: -.02em
        @media (max-width: 1024px)
          font-size: 20px
        @media (max-width: 767px)
          font-size: 18px
        @media (max-width: 639px)
          font-size: 16px
        @media (max-width: 420px)
          font-size: 21px
          line-height: 25px
        @media (max-width: 340px)
          font-size: 18px
          line-height: 24px
      .contacts-link
        display: inline-block
        margin: 92px 0 0
        @media (max-width: 1024px)
          margin: 62px 0 0
        @media (max-width: 959px)
          margin: 35px 0
        @media (max-width: 767px)
          padding: 10px 15px
          font-size: 16px
        @media (max-width: 639px)
          width: 120%
          text-align: center
        @media (max-width: 575px)
          width: 100%
          position: absolute
          bottom: 0
          left: 0
          font-size: 18px
          padding: 15px 15px 13px
        @media (max-width: 340px)
          font-size: 16px
          padding: 12px 10px 10px
    &__right-side
      flex: 0 0 65%
      max-width: 65%
      padding-top: 15px
      @media (max-width: 1024px)
        flex: 0 0 60%
        max-width: 60%
      @media (max-width: 959px)
        flex: 0 0 50%
        max-width: 50%
      @media (max-width: 639px)
        padding-top: 0
      @media (max-width: 575px)
        flex: 0 0 100%
        max-width: 100%
        margin-top: 30px
        padding-bottom: 90px
      @media (max-width: 420px)
        padding-bottom: 120px
      .map-image
        display: block
        width: auto
        max-width: 100%
        margin: 0 0 0 auto
        position: relative
        left: 10px
        @media (max-width: 575px)
          left: 0
  &__content
    margin-top: 52px
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    @media (max-width: 959px)
      margin-top: 35px
    @media (max-width: 420px)
      margin-top: 15px
    &__column
      flex: 0 0 auto
      width: auto
      &.main-column
        max-width: 215px
        @media (max-width: 1024px)
          max-width: 240px
          white-space: nowrap
        .logo-link
          display: inline-block
          .logo-image
            display: block
            width: auto
            max-width: 100%
        p
          color: #fff
          font-size: 12px
          line-height: 17px
          letter-spacing: -.02em
          margin-top: 15px
          &.copyright
            color: rgba(#fff, .4)
            margin-top: 20px
      &.menu-column
        margin: 0 auto
        max-width: 325px
        @media (max-width: 1024px)
          max-width: 240px
          margin: 0 0 0 auto
        @media (max-width: 959px)
          max-width: 100%
          margin: 0 0 35px
          flex: 0 0 100%
          order: -1
        @media (max-width: 639px)
          order: 0
          flex: 1 1 auto
          max-width: 50%
          margin: 0 0 0 auto
        @media (max-width: 575px)
          order: -1
          flex: 0 0 100%
          max-width: 100%
          margin: 0 0 30px
        .footer-menu
          &__list
            display: flex
            flex-wrap: wrap
            align-items: flex-start
            justify-content: flex-start
            @media (max-width: 959px)
              justify-content: space-between
            @media (max-width: 639px)
              justify-content: flex-start
          &__item
            flex: 0 0 50%
            max-width: 50%
            margin-bottom: 10px
            @media (max-width: 959px)
              flex: 0 0 auto
              width: auto
              margin: 0
            @media (max-width: 639px)
              flex: 0 0 50%
              max-width: 50%
              margin: 0 0 10px 0
          &__link
            display: inline-block
            position: relative
            color: #fff
            font-family: DINCondensed, sans-serif
            font-weight: 400
            font-size: 16px
            line-height: 20px
            letter-spacing: .01em
            &:before
              content: ''
              display: block
              width: 0
              height: 1px
              background-color: $yellow-btn-color
              position: absolute
              bottom: 0
              left: 0
              transition: width .3s
            &:hover
              &:before
                width: 100%
      &.info-column
        margin-left: auto
        padding-top: 5px
        @media (max-width: 959px)
          padding-top: 0
        @media (max-width: 639px)
          margin: 30px 0 0
          flex: 0 0 100%
          max-width: 100%
        .info-row
          display: flex
          align-items: stretch
          justify-content: flex-start
          &:nth-of-type(1)
            border-bottom: 1px solid rgba(#fff, .2)
            padding-bottom: 20px
          &:nth-of-type(2)
            margin-top: 20px
          &__side
            flex: 0 0 auto
            width: auto
            @media (max-width: 420px)
              max-width: calc(50% - 10px)
            &:nth-of-type(2)
              width: 145px
              margin-left: 30px
              @media (max-width: 420px)
                margin-left: auto
        .phone-link
          display: inline-block
          color: #fff
          font-family: DINCondensed, sans-serif
          font-weight: 400
          font-size: 22px
          line-height: 22px
          letter-spacing: -.02em
          transition: color .3s
          &:hover
            color: $yellow-btn-color
        p
          color: #fff
          font-size: 12px
          line-height: 14px
          letter-spacing: -.02em
          margin-top: 5px
        .social-links
          display: flex
          align-items: center
          justify-content: flex-start
          min-height: 22px
          &__item
            flex: 0 0 auto
            width: auto
            margin: 0 12px 0 0
          &__link
            display: flex
            align-items: center
            justify-content: center
            background-color: #fff
            border-radius: 50%
            width: 22px
            height: 22px
            font-size: 14px
            line-height: 20px
            color: $main-red-color
            transition: background-color .3s, color .3s
            .icon-paper-plane
              font-size: 12px
              padding-right: 2px
            &:hover
              background-color: $yellow-btn-color
              color: #000
          & + p
            margin-top: 8px
        .partners-list
          display: flex
          align-items: center
          justify-content: flex-start
          &__item
            flex: 0 0 auto
            width: auto
            .partner-image
              display: block
              width: auto
              max-width: 100%
              margin: 0
        .check-list
          display: flex
          align-items: center
          justify-content: flex-end
          &__item
            flex: 0 0 auto
            width: auto
            margin-left: 20px
            min-height: 32px
            display: flex
            align-items: center
            justify-content: center
            .check-image
              display: block
              width: auto
              max-width: 100%