.section-title-row {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  position: relative;
}
.section-title-row:after {
  content: url("../images/common/svg/title_btn.svg");
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 1 1 auto;
  max-width: 100px;
  height: 3px;
  padding-top: 3px;
  background: linear-gradient(270deg, #DA0C39 2.11%, rgba(218, 12, 57, 0) 98.45%);
  position: relative;
  top: 3px;
  margin: 0 3px 0 30px;
  transition: max-width 3s;
}
@media (max-width: 639px) {
  .section-title-row:after {
    display: none;
  }
}
.section-title-row.animated:after {
  max-width: 100%;
}

.section-title {
  display: inline-block;
  font-family: DINCondensed, sans-serif;
  font-weight: 400;
  font-size: 42px;
  line-height: 110%;
  letter-spacing: -0.02em;
  color: #000;
  position: relative;
}
@media (max-width: 1023px) {
  .section-title {
    font-size: 38px;
  }
}
@media (max-width: 767px) {
  .section-title {
    font-size: 34px;
  }
}
@media (max-width: 420px) {
  .section-title {
    font-size: 32px;
    line-height: 32px;
  }
}

.btn-style {
  font-family: DINCondensed, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.03em;
